import axios from "axios";
interface APIEntry {
  API: string;
  Description?: string;
}

const baseUrl = "https://api.publicapis.org/entries";
export const fetchBase = async (): Promise<APIEntry[]> => {
  try {
    const { data }: { data: { entries: APIEntry[] } } = await axios.get(
      baseUrl
    );
    const listInfo: APIEntry[] = data.entries.slice(10, 26);
    return listInfo;
  } catch (err) {
    console.log(err);
    throw err;
  }
};
export const fetchData = async (): Promise<void> => {
  try {
    const localItems = JSON.parse(localStorage.getItem("contact") || "[]");
    if (localItems.length > 0) return;
    const data: APIEntry[] = await fetchBase();
    localStorage.setItem("contact", JSON.stringify(data));
  } catch (err) {
    console.log(err);
  }
};

export const fetchTextChat = async (
  chatId: string,
  setLoading: Function,
  setList: Function
): Promise<void> => {
  try {
    setLoading(true);
    const data: APIEntry[] = await fetchBase();
    const filter = data.filter(({ API }) => API === chatId);
    setList(filter);
  } catch (err) {
    console.log(err);
  } finally {
    setLoading(false);
  }
};
