import { ChangeEvent, FC, useState } from "react";
import "./text.css";
import "./textError.css";
interface ObjNotice {
  text: string;
}

export const Text: FC = () => {
  const [text, setText] = useState("");
  const [textArray, setTextArray] = useState<ObjNotice[]>([]);
  const [errHidden, setErrHidden] = useState(false);
  const handleOnChange = (e: ChangeEvent<HTMLInputElement>) => {
    setText(e.currentTarget.value);
  };
  const changeBooleon = () => {
    setErrHidden(true);
    setTimeout(() => {
      setErrHidden(false);
    }, 2000);
  };
  const handleSubmit = (e: ChangeEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!text) {
      changeBooleon();
      return;
    }
    const arrMessage: ObjNotice = { text: text };
    setTextArray((prevTextArray) => [...prevTextArray, arrMessage]);
    setText("");
  };

  return (
    <div className="text-container">
      <div className="chat">
        {errHidden && <div className="text-error">Please, enter text !</div>}
        {textArray.map((message, index) => (
          <div className="message-wrap" key={index}>
            <p>{message.text}</p>
          </div>
        ))}
      </div>
      <form className="chat-input" onSubmit={handleSubmit}>
        <input
          type="text"
          placeholder="Enter a message"
          value={text}
          onChange={handleOnChange}
        />
        <button type="submit">Отправить</button>
      </form>
    </div>
  );
};
